<template>
  <slot>
    <transition name="fade">
      <img
        v-show="show"
        @click="scrollTop"
        class="top-page"
        src="https://img.icons8.com/material-outlined/96/000000/circled-up.png"
      />
    </transition>
  </slot>
</template>
<script>
//how to ipload
export default {
  data() {
    return {
      show: null,
    };
  },
  methods: {
    scrollTop() {
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
    onscroll() {
      this.show = !this.show;
      this.show = window.scrollY;
      if (this.show >= 300) {
        this.show = true;
        return;
      }
      this.show = false;
    },
  },
  created() {
    window.addEventListener("scroll", this.onscroll);
    this.onscroll();
  },
};
</script>

<style scoped>
.top-page {
  border: none;
  font-weight: bold;
  border-radius: 4px;
  position: fixed;
  bottom: 20px;
  right: 8px;
  cursor: pointer;
  width: 40px;
  z-index: 1000;
  transition: 1s ease;
}
.fade-enter-active,
.fade-leave-active {
  transition: 1s ease-in;
}
.fade-enter-from,
.fade-leave-to {
  /* transform: translateY(100px); */
  opacity: 0.2;
}
img:hover {
  transform: scale(1.1);
  transition: 1s ease;
}
</style>
